import VueRouter from 'vue-router'
import Vue from 'vue'

const Main = () => import('@/layout/main/index.vue')

const H5Editor = () => import('@/views/h5Editor/index.vue')
const WebTest = () => import('@/views/webTest/index.vue')

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  routes: [{
    path: '/',
    component: Main,
    children: [
      {
        path: 'h5-editor',
        component: H5Editor
      },
      {
        path: 'webtest',
        component: WebTest
      }
    ]
  }]
})

export default router



