import * as types from '../mutationTypes'

export default {
  /**
   * 保存登录token
   */
  setToken: ({ commit }, token) => {
    commit(types.SET_TOKEN, token)
  },
  setTest: ({ commit }, test) => {
    commit(types.SET_TEST, test)
  }
}



