import * as types from '../mutationTypes'

const state = {
  // 登录token
  token: ''
}
const mutations = {
  /**
   * 登录token
   */
  [types.SET_TOKEN] (state, token) {
    state.token = token
    window.localStorage.setItem('token', token)
  }
}

export default {
  state,
  mutations
}



