import * as types from '../mutationTypes'
import { isPlainObject, isArray } from '@/utils/util'

const state = {
  elementList: [],
  selectElementId: ''
}
const mutations = {
  // type : add(新增元素) editor(编辑元素) delete(删除元素)
  [types.SET_ELEMENT_LIST_ITEM_CAPTURE] (state, { type, element }) {
    if (type == 'add') {
      if (isPlainObject(element)) {
        state.elementList.push(element)
      } else if (isArray(element)) {
        state.elementList = state.elementList.concat(element)
      }
    } else if (type == 'editor') {
      const index = state.elementList.findIndex(item => item.elementId == element.elementId)
      if (index > -1) {
        state.elementList.splice(index, 1, element)
      } else {
        state.elementList.push(element)
      }
    } else if (type == 'delete') {
      const index = state.elementList.findIndex(item => item.elementId == element.elementId)
      if (index > -1) {
        state.elementList.splice(index, 1)
      }
    }
  },
  [types.SET_ELEMENT_ID] (state, elementId) {
    state.selectElementId = elementId
  }
}
export default {
  state,
  mutations
}



