const _toString = Object.prototype.toString
let antiShakeTimer = 0

const _getType = (val) => {
  return _toString.call(val).slice(8, -1).toLowerCase()
}

export const isPlainObject = (obj) => {
  return _toString.call(obj) === '[object Object]'
}

export const isObject = (obj) => {
  return obj !== null && typeof obj === 'object'
}

export const isArray = (obj) => {
  return _toString.call(obj) === '[object Array]'
}

export const antiShake = (fn, time) => {
  clearTimeout(antiShakeTimer)
  antiShakeTimer = setTimeout(fn, time)
}

export const debounce = (func, wait) => {
  let timer
  return function() {
    const args = arguments
    if (timer) clearTimeout(timer)
    timer = setTimeout(() => {
      func.apply(this, args)
    }, wait)
  }
}

export const clone = (val) => {
  const type = _getType(val)
  if (type === 'array') {
    const arr = []
    for (const i in val) {
      arr[i] = clone(val[i])
    }
    return arr
  } else if (type === 'object') {
    const obj = {}
    for (const i in val) {
      // eslint-disable-next-line no-prototype-builtins
      if (val.hasOwnProperty(i)) {
        obj[i] = clone(val[i])
      }
    }
    return obj
  }
  return val
}



