/* eslint-disable no-case-declarations */
import { clone } from 'front-uitls'
const deepDiff = require('deep-diff')

const SET_VUEX_IS_CAN_UNDO = 'SET_VUEX_IS_CAN_UNDO'
const SET_VUEX_IS_CAN_REDO = 'SET_VUEX_IS_CAN_REDO'

class VuexUndo {
  vuexStore = null
  undoList = []
  redoList = []
  undoKey = 'UNDOKEYMARK'
  redoKey = 'REDOKEYMARK'
  captureActionMask = '_CAPTURE'
  defaultMutationsMarkKey = 'DEFAULT_MUTATIONS_MARK_KEY'
  captureStateList = []

  initVuexUndoConfig (config) {
    config && config.captureActionMask && (this.captureActionMask = config.captureActionMask)
    config && config.store && (this.vuexStore = config.store)
  }

  madeKeyName(modulesName, key) {
    return `${modulesName}_${key}`
  }

  undoableConfig (vuexConfig = {}) {
    const self = this
    const { modules = {}, actions = {}, mutations = {}, state = {}, getters = {} } = vuexConfig
    const modulesKeys = modules ? Object.keys(modules) : []

    // init vuex state isCanUndo isCanRedo
    state.VUEX_IS_CAN_UNDO = false
    state.VUEX_IS_CAN_REDO = false
    getters.isVuexCaptureHaveUndo = state => state.VUEX_IS_CAN_UNDO
    getters.isVuexCaptureHaveRedo = state => state.VUEX_IS_CAN_REDO
    mutations[SET_VUEX_IS_CAN_UNDO] = function (state, isCanUndo) {
      state.VUEX_IS_CAN_UNDO = isCanUndo
    }
    mutations[SET_VUEX_IS_CAN_REDO] = function (state, isCanRedo) {
      state.VUEX_IS_CAN_REDO = isCanRedo
    }

    modulesKeys.push(self.defaultMutationsMarkKey)
    for (let i = 0; i < modulesKeys.length; i++) {
      if ((modules[modulesKeys[i]] && modules[modulesKeys[i]].mutations) || modulesKeys[i] === self.defaultMutationsMarkKey) {
        const _mutations = modulesKeys[i] === self.defaultMutationsMarkKey ? mutations : modules[modulesKeys[i]].mutations || {}
        for (const k in _mutations) {
          const fn = _mutations[k]
          _mutations[k] = function () {
            const arg = Array.prototype.slice.apply(arguments)
            const state = clone(arg[0])
            const captureMark = arg[1] && arg[1].vuexCaptureMark ? arg[1].vuexCaptureMark : ''
            fn.apply(this, arg)
            if (self.isCaptureAction(k) && captureMark != 'noCapture') {
              if (captureMark == 'start') {
                self.captureStateList = [clone(arg[0])]
              } else if (captureMark == 'end' && self.captureStateList.length == 1) {
                self.captureStateList.push(clone(arg[0]))
                self.diffCompare(...[k, modulesKeys[i], self.captureStateList[0], self.captureStateList[1]])
                self.captureStateList.length = 0
              } else {
                if (self.captureStateList.length == 0) {
                  self.diffCompare(...[k, modulesKeys[i], state, ...arg])
                }
              }
            }
          }
        }
        const bool = Object.keys(_mutations).some(k => self.isCaptureAction(k))
        if (bool) {
          _mutations[self.madeKeyName(modulesKeys[i], self.undoKey)] = function (state) {
            self.undoAction(state)
          }
          _mutations[self.madeKeyName(modulesKeys[i], self.redoKey)] = function (state) {
            self.redoAction(state)
          }
        }
      }
    }

    actions.vuex_action_undo = function ({ commit }) {
      self.undo({ commit })
    }
    actions.vuex_action_redo = function ({ commit }) {
      self.redo({ commit })
    }
    actions.vuex_update_undo_redo_state = function ({ commit }) {
      commit(SET_VUEX_IS_CAN_UNDO, self.undoList.length > 0)
      commit(SET_VUEX_IS_CAN_REDO, self.redoList.length > 0)
    }
    return vuexConfig
  }

  redoAction (state) {
    const actionLastItem = this.redoList.pop()
    const actionPath = actionLastItem.actionPath || []
    if (actionPath && actionPath.length) {
      for (let i = 0; i < actionPath.length; i++) {
        this.operate(actionPath[i], state, 'redo')
      }
      this.undoList.push(actionLastItem)
    }
  }

  undoAction (state) {
    const actionLastItem = this.undoList.pop()
    const actionPath = actionLastItem.actionPath || []
    if (actionPath && actionPath.length) {
      for (let i = 0; i < actionPath.length; i++) {
        this.operate(actionPath[i], state, 'undo')
      }
      this.redoList.push(actionLastItem)
    }
  }

  operate (actionItem, state, type) {
    const path = actionItem.path || []
    const o = path.reduce((pre, it) => pre[it], state)
    if (type === 'undo' || type === 'redo') {
      const val = type === 'undo' ? actionItem.lhs : actionItem.rhs
      switch (actionItem.kind) {
        case 'N': // add newly property
          const _val = type === 'undo' ? undefined : val
          this.setObj(state, path, _val)
          break
        case 'D': // deleted property
          this.setObj(state, path, val)
          break
        case 'E': // edited property
          this.setObj(state, path, val)
          break
        case 'A': // change occurred within an array
          const index = actionItem.index
          const oA = o[index]
          if (oA && oA.item && oA.item.path) {
            this.operate(oA.item, oA, type)
          } else {
            const _item = actionItem.item
            const _val = type === 'undo' ? _item.lhs : _item.rhs
            if (_val === undefined) {
              o.splice(index, 1)
            } else {
              o.splice(index, 1, _val)
            }
          }
          break
      }
    }
  }

  setObj (o, path, value) {
    const _path = path.concat()
    const lastKey = _path.pop()
    const ob = _path.reduce((pre, it) => pre[it], o)
    if (value === undefined) {
      delete ob[lastKey]
    } else if (value !== undefined) {
      ob[lastKey] = value
    }
  }

  diffCompare () {
    const arg = Array.prototype.slice.apply(arguments)
    const actionName = arg[0]
    const modulesKey = arg[1]
    const preState = arg[2]
    const curentState = arg[3]
    const actionPath = deepDiff.diff(preState, curentState)
    if (actionPath) {
      this.capture({
        actionName,
        actionPath,
        undoActionKey: this.madeKeyName(modulesKey, this.undoKey),
        redoActionKey: this.madeKeyName(modulesKey, this.redoKey)
      })
    }
  }

  isCaptureAction (action) {
    return action.slice(-this.captureActionMask.length) === this.captureActionMask
  }

  undo ({ commit }) {
    if (this.undoList && this.undoList.length) {
      const popItem = this.undoList[this.undoList.length - 1]
      commit(popItem.undoActionKey)
      this.vuexStore && this.vuexStore.dispatch('vuex_update_undo_redo_state')
    }
  }

  redo ({ commit }) {
    if (this.redoList && this.redoList.length) {
      const popItem = this.redoList[this.redoList.length - 1]
      commit(popItem.redoActionKey)
      this.vuexStore && this.vuexStore.dispatch('vuex_update_undo_redo_state')
    }
  }

  capture (o) {
    this.undoList.push(o)
    this.vuexStore && this.vuexStore.dispatch('vuex_update_undo_redo_state')
  }
}
const vuexUndo = window.vuexUndo = new VuexUndo()
export const install = (Vue, options) => {
  vuexUndo.initVuexUndoConfig(options)
}

export default (vuexConfig) => {
  return vuexUndo.undoableConfig(vuexConfig)
}



