import * as types from '../mutationTypes'

const state = {
  // 登录token
  token: '',
  test: { a: 3, c: 4 },
  testmm: []
}
const mutations = {
  /**
   * 登录token
   */
  [types.SET_TOKEN] (state, token) {
    state.token = token
    window.localStorage.setItem('token', token)
  },
  [types.SET_TEST] (state, obj) {
    state.test = obj
  },
  [types.SET_TEST_MM_ADD] (state, obj) {
    state.testmm.push(obj)
  },
  [types.SET_TEST_MM_DEL] (state, index) {
    state.testmm.splice(index, 1)
  },
  [types.SET_TEST_MM_CONCANT] (state, arr) {
    state.testmm = state.testmm.concat(arr || [])
  },
  [types.SET_TEST_MM_EDITE] (state, obj) {
    const index = state.testmm.findIndex(item => item.id == obj.id)
    console.log(2222, index)
    if (index != -1) {
      state.testmm.splice(index, 1, obj)
    }
  }
}

export default {
  state,
  mutations
}



