import * as types from '../mutationTypes'

export default {
  setProjectIndex__CAPTURE (state, index) {
    state.projectIndex = index
  },
  [types.SET_HOVER_ELEMENT_ID] (state, elementId) {
    state.hoverElementId = elementId
  },
  [types.SET_IS_MOVE] (state, isMove) {
    state.isMove = isMove
  },
  [types.SET_IS_ROTATE] (state, isRotate) {
    state.isRotate = isRotate
  },
  [types.SET_IS_EDITOR] (state, isEditor) {
    state.isEditor = isEditor
  }
}



