import * as types from '../mutationTypes'
export default {
  setElementListItemCapture: ({ commit }, { type, element, vuexCaptureMark }) => {
    commit(types.SET_ELEMENT_LIST_ITEM_CAPTURE, { type, element, vuexCaptureMark })
  },
  setElementId: ({ commit }, elementId) => {
    commit(types.SET_ELEMENT_ID, elementId)
  },
  setHoverElementId: ({ commit }, elementId) => {
    commit(types.SET_HOVER_ELEMENT_ID, elementId)
  },
  setIsMove: ({ commit }, isMove) => {
    commit(types.SET_IS_MOVE, isMove)
  },
  setIsRotate: ({ commit }, isRotate) => {
    commit(types.SET_IS_ROTATE, isRotate)
  },
  setIsEditor: ({ commit }, isEditor) => {
    commit(types.SET_IS_EDITOR, isEditor)
  }
}



